import TabBar from '@/components/TabBar/index.vue'
import LeftMenu from '@/components/LeftMenu/index.vue'
import $ from 'jquery'

export default {
    data() {
        return {
            username: '迎财神',
            photo: '/img/pic_logo_m.png',
            totalAmount: 0,
            amount: 0,
            lockAmount: 0,
            txAmount: 0,
            list:[]
        }
    },
    created: function () {
        //获取用户基本信息
        $.ajax({
            type: "GET",
            url: "/Mobile/Api/Info",
            data: {},
            dataType: "json",
            success: function(rst){
                if(rst.status==0){
                    this.username = rst.U_NickName;
                    this.photo = rst.U_Thumbnail;
                    this.amount = rst.U_Amount;
                    this.lockAmount = rst.U_LockAmount;
                    this.totalAmount = rst.U_Amount+rst.U_LockAmount;
                    this.txAmount = 0;
                }
                else{
                    alert(rst.msg);
                }
            }
        });
        //获取账单记录
        $.ajax({
            type: "GET",
            url: "/Mobile/Api/AmountList",
            data: {page:1},
            dataType: "json",
            success: function(rst){
                if(rst.status==0){
                  this.list= rst.data;
                }
                else{
                    alert(rst.msg);
                }
            }
        });
    },
    components: {
        TabBar,
        LeftMenu
    },
    methods: {
        getUserInfo(){
        },
        getAmountList(){
        },
        btnChongZhi(){
            location.href="/Mobile/Member/Recharge";
        }
    }
}